*,html {
  padding: 0;
  margin: 0;
  font-family: 'Alata', sans-serif;
}

backgroundImage {
  filter: brightness(10%);
}

.info-box {
  position: fixed;
  display: flex;
  left: 0;
  top: 0;
  background-color: rgba(0,0,0,0.25);
  width: 100%;
  height: 100vh;
  overflow-y: scroll;

}

.box {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 400px;
  background-color: white;
  margin: 300px 0;
  padding: 20px 0px 20px 0px;
  overflow: auto;
}

.close-button {
  cursor: pointer;
  border: 1px solid #999;
  padding: 10px;
  position: fixed;
  right: 5%;
  top: 30px;
}

.fish {
  max-height: 20%;
  max-width: 20%;
  cursor: pointer;
}

.info-text {
  font-family: Arial, Helvetica, sans-serif;
  width: 75%;
  margin: auto;
}

.info-image {
  border-radius: 250px;
}
.App {
  margin: 0;
  border: 0;
}

@keyframes rotation
{
  0%
  {
    transform: rotate(0deg);
  }
  50%
  {
    transform: rotate(1.8deg);
  }
  100%
  {
    transform: rotate(0deg);
  }
}

@keyframes butterflyfish
{
  from
  {
    top: 10px;
    left: 1500px;
  }
  to
  {
    top: 80px;
    left: 1200px;
  }
}

#butterflyfish
{
  width: 14%;
  position: absolute;
  left: 1200px;
  top: 80px;
  rotate: 2deg;

  animation: butterflyfish 1s alternate, rotation .8s infinite;
  animation-delay: 0s, 1s;

  transition: ease-in-out .2s;
}

#butterflyfish:hover
{
  cursor: pointer;
  filter: brightness(80%);
}

