#webAq
{
  color: white;
  font-size: 140px;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: black;
  /* text-shadow: -4px -4px 0 #000, 4px -4px 0 #000, -4px 4px 0 #000, 4px 4px 0 #000; */

  position: absolute;
  top: 34%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);

  transition: .4s;
}

#webAq:hover
{
  color: #5fe994;
  cursor: default;
}

#click2enter
{
  color: white;
